import React from 'react';
import { Link } from 'react-router-dom';
import { Input, DatePicker, Button, Modal } from 'antd';
import { CardComponent, CommonPage, SearchBarComponent, StatisticsComponent} from "../components";
const { RangePicker } = DatePicker;
class IndividualCourseList extends React.Component {
    render(){
        const columns = [
            {
                title: "",
                dataIndex: "create_time",
                key: "create_time"
            },{
                title: "ID",
                dataIndex: "id",
                key: "id"
            },{
                title: "",
                dataIndex: "name",
                key: "name"
            },{
                title: "",
                dataIndex: "birthday",
                key: "birthday"
            },{
                title: "",
                dataIndex: "phone",
                key: "phone"
            },{
                title: "()",
                dataIndex: "fee",
                key: "fee"
            },{
                title: "",
                dataIndex: "coach",
                key: "coach"
            },{
                title: "",
                dataIndex: "remark",
                key: "remark"
            }
        ];
        return (
            <CommonPage title="" table={{dataSource:[], columns:columns}}>
                <SearchBarComponent
                    left={[{
                        label: "",
                        Element: RangePicker,
                        props: {

                        }
                    },{
                        label: "",
                        Element: Input,
                        props: {
                            placeholder: ""
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: ""
                    },{
                        Element: Button,
                        value: "CSV"
                    },{
                        Element: Link,
                        value: <Button type="primary"></Button>,
                        props: {
                            to: '/course/individual/list/add'
                        }
                    }]}/>
                <CardComponent title="">
                    <StatisticsComponent>
                        <StatisticsComponent.Item label="-"/>
                        <StatisticsComponent.Item label="-"/>
                        <StatisticsComponent.Item label="-"/>
                        <StatisticsComponent.Item label=""/>
                        <StatisticsComponent.Item label=""/>
                        <StatisticsComponent.Item label="()"/>
                    </StatisticsComponent>
                </CardComponent>
            </CommonPage>
        )
    }
}

export default IndividualCourseList;


