import React from 'react';
import { DatePicker, Input,Button } from 'antd';
import { CommonPage, SearchBarComponent} from "../components";
const { RangePicker } = DatePicker;
class CheckManageList extends React.Component{
    render(){
        const dataSource = [];
        const columns = [{
            title: "",
            dataIndex: "check_time",
            key: "check_time"
        },{
            title: "ID",
            dataIndex: "id",
            key: "id"
        },{
            title: "",
            dataIndex: "class_time",
            key: "class_time"
        },{
            title: "",
            dataIndex: "location",
            key: "location"
        },{
            title: "",
            dataIndex: "monitor",
            key: "monitor"
        },{
            title: "",
            dataIndex: "assistant",
            key: "assistant"
        },{
            title: "",
            dataIndex: "status",
            key: "status"
        },{
            title: "",
            dataIndex: "student_total",
            key: "student_total"
        },{
            title: "",
            dataIndex: "student_checked",
            key: "student_checked"
        },{
            title: "",
            dataIndex: "operate",
            key: "operate"
        }];
        return (
            <CommonPage title="" table={{dataSource, columns, total: 20}}>
                <SearchBarComponent
                    left={[{
                        label: "",
                        Element: RangePicker,
                        props: {
                            style: {
                                width: 200
                            }
                        }
                    },{
                        label: "",
                        Element: Input,
                        props: {
                            placeholder: ""
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: ""
                    },{
                        Element: Button,
                        value: "CSV"
                    }]}
                />
            </CommonPage>
        )
    }
}

export default CheckManageList;


