import React from 'react';
import { Input, Button, DatePicker } from 'antd';
import { CommonPage, SearchBarComponent} from '../components/index';
class SpecialCourseSignUpList extends React.Component{
    render(){
        const { loading } = this.props;
        const dataSource = [];

        const columns = [{
            title: '',
            dataIndex: 'create_time',
            key: 'create_time',
        }, {
            title: 'ID',
            dataIndex: 'id',
            key: 'id',
        }, {
            title: '',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '',
            dataIndex: 'time',
            key: 'time',
        }, {
            title: '()',
            dataIndex: 'fee',
            key: 'fee',
        }, {
            title: '',
            dataIndex: 'total',
            key: 'total',
        }, {
            title: '',
            dataIndex: 'left',
            key: 'left',
        }, {
            title: '(//)',
            dataIndex: 'record',
            key: 'record',
        },{
            title: '',
            dataIndex: 'status',
            key: 'status',
        }
        ];
        return (
            <CommonPage title="/" table={{dataSource, columns, total: 0}}>
                <SearchBarComponent
                    left={[{
                        label: "",
                        Element: Input,
                        props: {}
                    }]}
                    right={[{
                        Element: Button,
                        value: ""
                    },{
                        Element: Button,
                        value: "CSV"
                    }]}/>
            </CommonPage>
        )
    }
}

export default SpecialCourseSignUpList;


