import React from 'react';
import { Input, Button, DatePicker, Select } from 'antd';
import { CommonPage, SearchBarComponent, CardComponent, StatisticsComponent } from '../components/index';
const { RangePicker } = DatePicker;
class SpecialCourseSignUpList extends React.Component{
    render(){
        const { loading } = this.props;
        const dataSource = [];

        const columns = [{
            title: 'ID',
            dataIndex: 'id',
            key: 'id',
        }, {
            title: '',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '',
            dataIndex: 'class_time',
            key: 'class_time',
        }, {
            title: '',
            dataIndex: 'location',
            key: 'location',
        }, {
            title: '(//)',
            dataIndex: 'sign_stat',
            key: 'sign_stat',
        }, {
            title: '(//)',
            dataIndex: 'fee_stat',
            key: 'fee_stat',
        },{
            title: '',
            dataIndex: 'operate',
            key: 'operate',
        }
        ];
        return (
            <CommonPage title="" table={{dataSource, columns, total: 0}}>
                <SearchBarComponent
                    left={[{
                        label: "",
                        Element: RangePicker,
                        props: {
                            style:{
                                width: 200
                            }
                        }
                    },{
                        label: "",
                        Element: Input,
                        props: {}
                    }]}
                    right={[{
                        Element: Button,
                        value: ""
                    },{
                        Element: Button,
                        value: "CSV"
                    }]}/>
                <CardComponent title="">
                    <StatisticsComponent>
                        <StatisticsComponent.Item label="-" detail="37,346"/>
                        <StatisticsComponent.Item label="-" detail="30,000"/>
                        <StatisticsComponent.Item label="-"/>
                        <StatisticsComponent.Item label=""/>
                        <StatisticsComponent.Item label=""/>
                        <StatisticsComponent.Item label="()"/>
                    </StatisticsComponent>
                </CardComponent>
            </CommonPage>
        )
    }
}

export default SpecialCourseSignUpList;


