import React from 'react';
import { Link } from 'react-router-dom';
import { Select, Input,Button } from 'antd';
import { CommonPage, SearchBarComponent} from "../components";
const { Option } = Select;
class CoachManageList extends React.Component{
    render(){
        const dataSource = [];
        const columns = [{
            title: "ID",
            dataIndex: "id",
            key: "id"
        },{
            title: "",
            dataIndex: "avatar",
            key: "avatar"
        },{
            title: "",
            dataIndex: "name",
            key: "name"
        },{
            title: "",
            dataIndex: "gender",
            key: "gender"
        },{
            title: "",
            dataIndex: "phone",
            key: "phone"
        },{
            title: "",
            dataIndex: "grade",
            key: "grade"
        },{
            title: "",
            dataIndex: "create_time",
            key: "create_time"
        },{
            title: "",
            dataIndex: "status",
            key: "status"
        },{
            title: "",
            dataIndex: "operate",
            key: "operate"
        }];
        return (
            <CommonPage title="" table={{dataSource, columns, total: 20}}>
                <SearchBarComponent
                    left={[{
                        label: "",
                        Element: Select,
                        props: {
                            style: {
                                width: 200
                            },
                            placeholder: ""
                        },
                        children: [<Option key="111">666</Option>]
                    },{
                        label: "",
                        Element: Input,
                        props: {
                            placeholder: ""
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: ""
                    },{
                        Element: Button,
                        value: "CSV"
                    },{
                        Element: Link,
                        value: <Button type="primary"></Button>,
                        props: {
                            to: '/manage/coach/add'
                        }
                    }]}
                />
            </CommonPage>
        )
    }
}
export default CoachManageList;


