import React from 'react';
import { Avatar, Button, DatePicker, Modal, Select } from 'antd';
import { InputComponent } from '../components';
class IndividualCourseAdd extends React.Component{
    render(){
        return (
            <Modal visible={true} title="" width={700}>
                <form action="javascript:;" className="q-pwd-modify" style={{margin: '0 auto'}}>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label"></label>
                        <div className="q-form-detail">
                            <InputComponent placeholder=""/>
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label"></label>
                        <div className="q-form-detail">
                            <Select>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                            </Select>
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label"></label>
                        <div className="q-form-detail">
                            <DatePicker style={{width: '100%'}}/>
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label"></label>
                        <div className="q-form-detail">
                            <InputComponent
                                placeholder=""
                            />
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label"></label>
                        <div className="q-form-detail">
                            <InputComponent
                                placeholder=""
                            />
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label"></label>
                        <div className="q-form-detail">
                            <Select placeholder="">
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                            </Select>
                        </div>
                    </div>
                    <div className="q-form-group q-horizontal q-mrg-b25">
                        <label className="q-form-label q-align-right q-label"></label>
                        <div className="q-form-detail">
                            <InputComponent
                                placeholder=""
                                help="15"
                            />
                        </div>
                    </div>
                </form>
            </Modal>
        )
    }
}

export default IndividualCourseAdd;


