import React from 'react';
import { Link } from 'react-router-dom';
import { Input, Button } from 'antd';
import { CommonPage, SearchBarComponent } from '../components';
class SpecialCourseList extends React.Component{
    render(){
        const dataSource = [{
            key: 111,
            name: "",
            time: "2017-07-29",
            number: 32,
            monitor: "",
            create_time: "2017-07-29 22:00",
            status: '',
            link: "www.baidu.com",
            operation: "111"
        }];

        const columns = [{
            title: 'ID',
            dataIndex: 'key',
            key: 'key',
        }, {
            title: '',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: '',
            dataIndex: 'time',
            key: 'time',
        }, {
            title: '',
            dataIndex: 'number',
            key: 'number',
        }, {
            title: '',
            dataIndex: 'monitor',
            key: 'monitor',
        }, {
            title: '',
            dataIndex: 'create_time',
            key: 'create_time',
        }, {
            title: '',
            dataIndex: 'status',
            key: 'status',
        }, {
            title: '',
            dataIndex: 'link',
            key: 'link',
        }, {
            title: '',
            dataIndex: 'operation',
            key: 'operation',
        }];
        return (
            <CommonPage title='' table={{dataSource, columns, total: 5, royKey: 'id'}}>
                <SearchBarComponent
                    left={[{
                        label: "",
                        Element: Input
                    }]}
                    right={[{
                        Element: Button,
                        value: ""
                    },{
                        Element: Button,
                        value: "CSV"
                    },{
                        Element: Link,
                        value: <Button type="primary"></Button>,
                        props: {
                            to: "/course/special/add"
                        }
                    }]}/>
            </CommonPage>
        )
    }
}
export default SpecialCourseList;



