import React from 'react';
import { Avatar, Select, Button, Modal } from 'antd';
import { CommonPage, InputComponent, AvatarSettingComponent } from '../../components';
import './add-edit.css';
const { Option } = Select;
class CoachTemplate extends React.Component{
    render(){
        const { subTitle, modalOption } = this.state;
        return (
            <CommonPage title=''>
                <section className="q-section">
                    <h3 className="q-section-title">{subTitle}</h3>
                    <div className="q-section-body">
                        <form action="javascript:;">
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label"></label>
                                <div className="q-form-detail">
                                    <Button></Button>
                                    <Avatar size="small"/>
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label"></label>
                                <div className="q-form-detail">
                                    <InputComponent
                                        placeholder=""
                                    />
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label"></label>
                                <div className="q-form-detail">
                                    <Select>
                                        <Option value="1"></Option>
                                        <Option value="2"></Option>
                                    </Select>
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label"></label>
                                <div className="q-form-detail">
                                    <InputComponent
                                        placeholder=""
                                    />
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label"></label>
                                <div className="q-form-detail">
                                    <Select>
                                        <Option value="1"></Option>
                                        <Option value="2"></Option>
                                    </Select>
                                </div>
                            </div>
                            <div className="q-form-group q-horizontal q-mrg-b25">
                                <label className="q-form-label q-align-right q-label"></label>
                                <div className="q-form-detail">
                                    <InputComponent
                                        placeholder=""
                                        help="15"
                                        maxLength="15"/>
                                </div>
                            </div>
                        </form>
                    </div>
                </section>
                <AvatarSettingComponent visible={true}/>
            </CommonPage>
        )
    }
}

export default CoachTemplate;


