import React from 'react';
import { DatePicker, Input,Button } from 'antd';
import { CommonPage, SearchBarComponent } from "../components";
const { RangePicker } = DatePicker;
class SpecialSignDetail extends React.Component{
    render(){
        const dataSource = [];
        const columns = [{
            title: "ID",
            dataIndex: "id",
            key: "id"
        },{
            title: "",
            dataIndex: "create_time",
            key: "create_time"
        },{
            title: "",
            dataIndex: "avatar",
            key: "avatar"
        },{
            title: "",
            dataIndex: "nickname",
            key: "nickname"
        },{
            title: "",
            dataIndex: "name",
            key: "name"
        },{
            title: "",
            dataIndex: "gender",
            key: "gender"
        },{
            title: "",
            dataIndex: "birthday",
            key: "birthday"
        },{
            title: "",
            dataIndex: "phone",
            key: "phone"
        },{
            title: "()",
            dataIndex: "fee",
            key: 'fee'
        }];
        return(
            <CommonPage title="/" table={{dataSource, columns, total: 0}}>
                <SearchBarComponent
                    left={[{
                        label: "",
                        Element: RangePicker,
                        props: {
                            style: {
                                width: 200
                            }
                        }
                    },{
                        label: "",
                        Element: Input,
                        props: {
                            placeholder: ""
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: ""
                    },{
                        Element: Button,
                        value: "CSV"
                    }]}
                />
            </CommonPage>
        )
    }
}

export default SpecialSignDetail;


