import React from 'react';
import { Input,Button } from 'antd';
import { CardComponent, CommonPage, SearchBarComponent, StatisticsComponent} from "../components";
class StudentManageList extends React.Component{
    render(){
        const dataSource = [];
        const columns = [{
            title: "ID",
            dataIndex: "id",
            key: "id"
        },{
            title: "",
            dataIndex: "name",
            key: "name"
        },{
            title: "",
            dataIndex: "gender",
            key: "gender"
        },{
            title: "",
            dataIndex: "birthday",
            key: "birthday"
        },{
            title: "",
            dataIndex: "phone",
            key: "phone"
        },{
            title: "",
            dataIndex: "status",
            key: "status"
        },{
            title: "",
            dataIndex: "operate",
            key: "operate"
        }];
        return (
            <CommonPage title="" table={{dataSource, columns, total: 20}}>
                <SearchBarComponent
                    left={[{
                        label: "",
                        Element: Input,
                        props: {
                            placeholder: ""
                        }
                    }]}
                    right={[{
                        Element: Button,
                        value: ""
                    },{
                        Element: Button,
                        value: "CSV"
                    }]}
                />
                <CardComponent title="">
                    <StatisticsComponent>
                        <StatisticsComponent.Item label="-" detail="300"/>
                        <StatisticsComponent.Item label="-"/>
                        <StatisticsComponent.Item label="-"/>
                        <StatisticsComponent.Item label=""/>
                        <StatisticsComponent.Item label=""/>
                        <StatisticsComponent.Item label="()"/>
                    </StatisticsComponent>
                </CardComponent>
            </CommonPage>
        )
    }
}

export default StudentManageList;


