
import React from 'react';
import { Checkbox, TimePicker, Select, Cascader   } from 'antd';
import { CommonPage, StepsComponent, TabsComponent, PhoneComponent,InputComponent, FormComponent,MapComponent,RichTextComponent } from '../../components';
import { weeks } from "../../config/CONSTANT";
import addressOptions from '../../config/cascader-address-options';
import "../../../css/supplement.css";
const CheckBoxGroup = Checkbox.Group;
const Option = Select.Option;
class RegularCourseAddEditTemplate extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            activeKey:'2',
            inputElement: null,
            text: 'hello 111'
        };
    }
    render(){
        const footerConfig = {
            ok: {
                value: "",
                onClick: () => this.setState({
                    activeKey:'2'
                })
            },
            cancel: {
                value: '',
                onClick: () => this.setState({
                    activeKey:'1'
                })
            }
        };
        return (
            <CommonPage table={false} className="q-sub-page" title="" pageFooter={footerConfig}>
                <StepsComponent current={1}>
                    <StepsComponent.Item>1.</StepsComponent.Item>
                    <StepsComponent.Item>2.</StepsComponent.Item>
                </StepsComponent>
                <div className="q-pane">
                    <PhoneComponent className="q-pane-phone"/>
                    <div className="q-pane-rest q-mrg-l15">
                        <TabsComponent activeKey={this.state.activeKey}>
                            <TabsComponent.TabPane tab="" key="1">
                                <section className="q-sub-section">
                                    <h3 className="q-sub-section-title"></h3>
                                    <div className="q-sub-section-main">
                                        <FormComponent direction="vertical" >
                                            <FormComponent.Group  label="" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent
                                                    placeholder=""
                                                    maxLength="15"
                                                    error=""
                                                    help="15"
                                                />
                                            </FormComponent.Group>
                                            <FormComponent.Group label="" labelWidth="120px" marginBottom='0' style={{width: "330px"}}>
                                                <div className="q-multiline">
                                                    <CheckBoxGroup size="small">
                                                        {weeks.map((item) => (
                                                            <Checkbox size="small" value={item.value} key={item.value}>{item.label}</Checkbox>
                                                        ))}
                                                    </CheckBoxGroup>
                                                    <div className="q-timepicker-group">
                                                        <TimePicker/>
                                                        <span className="q-short-dash">-</span>
                                                        <TimePicker/>
                                                    </div>
                                                    <div className="q-timepicker-add">
                                                        <a className="link"></a>
                                                        <a className="link"></a>
                                                    </div>
                                                </div>
                                            </FormComponent.Group>
                                            <FormComponent.Group label="" labelWidth="120px" style={{width: "330px"}}>
                                                <Cascader options={addressOptions}/>
                                            </FormComponent.Group>
                                            <FormComponent.Group label="" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent placeholder="" wrapRef={(r) => this.getInputRef(r)}/>
                                            </FormComponent.Group>
                                        </FormComponent>
                                        <div className="q-map-container">
                                            <MapComponent getLatLng={(latLng) => console.log(latLng)} lat="23.17072" lng="113.34234" bigAddress="" inputElement={this.state.inputElement}/>
                                        </div>
                                    </div>
                                </section>
                                <section className="q-sub-section">
                                    <h3 className="q-sub-section-title"></h3>
                                    <div className="q-sub-section-main">
                                        <FormComponent direction="vertical" >
                                            <FormComponent.Group  label="" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent
                                                    placeholder=""
                                                    help="220.00"
                                                />
                                            </FormComponent.Group>
                                            <FormComponent.Group label="" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent placeholder=""/>
                                            </FormComponent.Group>
                                            <FormComponent.Group label="" labelWidth="120px" style={{width: "330px"}}>
                                                <InputComponent placeholder=""/>
                                            </FormComponent.Group>
                                            <FormComponent.Group label="" labelWidth="120px" style={{width: "330px"}}>
                                                <Select placeholder="">
                                                    <Option value=""></Option>
                                                </Select>
                                            </FormComponent.Group>
                                        </FormComponent>
                                    </div>
                                </section>
                            </TabsComponent.TabPane>
                            <TabsComponent.TabPane tab="" key="2">
                                <section className="q-sub-section">
                                    <h3 className="q-sub-section-title"></h3>
                                    <div className="q-sub-section-main">
                                        <RichTextComponent maxLength={500} text={this.state.text} getText={() => {}}/>
                                    </div>
                                </section>
                            </TabsComponent.TabPane>
                        </TabsComponent>
                    </div>
                </div>
            </CommonPage>
        )
    }
    componentDidMount(){

    }
    getInputRef(ref){
        if(ref && !this.state.inputElement){
            this.setState({
                inputElement: ref
            });
        }
    }
}
export default RegularCourseAddEditTemplate;




