import React from 'react';
import { Button } from 'antd';
import { Link } from 'react-router-dom';
import { CardComponent, CommonPage, InputComponent } from '../components/index';
import { passwordChecker } from '../utils';
class UserAccount extends  React.Component{
    constructor(props){
        super(props);
        this.state = {
            oldPwd: {
                value: '',
                hide: true,
                errMsg: ''
            },
            newPwd: {
                value: '',
                hide: true,
                errMsg: ''
            },
            repeatPwd: {
                value: '',
                hide: true,
                errMsg: ''
            }
        };
    }
    render(){
        return (
           <CommonPage title="">
                <CardComponent title="">
                    <div className="q-pwd-modify">
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label"></label>
                            <div className="q-form-detail">
                                <span></span>
                            </div>
                        </div>
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label "></label>
                            <div className="q-form-detail">
                                <span></span>
                            </div>
                        </div>
                    </div>
                </CardComponent>
                <CardComponent title="">
                    <form className="q-pwd-modify">
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label"></label>
                            <div className="q-form-detail">
                                <InputComponent
                                    type="password"
                                    placeholder=""
                                    help={<span><Link to="/forget"></Link></span>}
                                />
                            </div>
                        </div>
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label"></label>
                            <div className="q-form-detail">
                                <InputComponent
                                    type="password"
                                    placeholder=""
                                    help={<span>6</span>}
                                />
                            </div>
                        </div>
                        <div className="q-form-group q-horizontal q-mrg-b25">
                            <label className="q-form-label q-align-right q-label"></label>
                            <div className="q-form-detail">
                                <InputComponent
                                    type="password"
                                    placeholder=""
                                />
                            </div>
                        </div>
                        <div className="q-form-group q-btn">
                            <Button type="primary" tabIndex="4"></Button>
                        </div>
                    </form>
                </CardComponent>
           </CommonPage>
        )
    }
}
export default UserAccount;



